/* 最大宽度 */
@window-Width: 70%;

.page {
  padding: 32px;
  text-align: center;
}

/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}

.sidebar-header .icon {
  color: #2f8cf0;
}

.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  &:hover {
    color: #2d8cf0;
    text-decoration: underline;
  }
}

.v-note-wrapper {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}

.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}
























.author {
  box-sizing: border-box;
  width: @window-Width;
  min-height: 80vh;
  margin: 24px auto;
  overflow: hidden;
  padding-bottom: 24px;
  background-color: #fff;
  border-radius: 6px;
}

.item {
  width: 100%;
  padding: 48px 0;
  text-align: center;
  background: white;
}

.logo {
  width: 320px;
  margin: 32px auto 0;

  & img {
    width: 100%;
  }
}

a {
  color: #409EFF;
}

.text {
  padding: 0 32px 32px;
  line-height: 42px;
  font-size: 20px;
  color: #666;
  text-indent: 2em;
}

.content {
  padding-top: 32px;
}

.end {
  line-height: 42px;
  font-size: 20px;
  color: #666;
  text-indent: 2em;
}

@media screen and (min-width: 200px) and (max-width: 750px) {
  .author {
    width: 100%;
  }
}
